body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas:
    "header header header"
    "sidebar-left main sidebar-right"
"footer footer footer";
background-color: #282c34;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 70vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.incomes-container {
  grid-area: header;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 20px;
  margin-bottom: 20px;
  color : #ff8300;
}

.incomes {
  background-color: #485262;
  margin-left: 8px;
  color: white;
  padding: 1px 10px 1px 10px;
}

.toolbox-container {
  grid-area: sidebar-left;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.title {
  text-align: start;
  margin-left: 8px;
  color : #ff8300;
}


.toolbox-board {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;

  width: 150px;
  height: 100%;
  margin: 4px 0 10px 0;
  /*background-color: #f1efff;*/
}

.row {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}


.seller:hover {
  cursor: pointer;
}

.furnace:hover {
  cursor: pointer;
}

.crafter:hover {
  cursor: pointer;
}

.transporter:hover {
  cursor: pointer;
}

.starter:hover {
  cursor: pointer;
}

.cell {
  border: solid 2px grey;
  width: 50px;
  height: 50px;
  background-color: #FFFFFF;
}

.add_machine_active {
  background-color: blue;
}

.add_machine:hover {
  cursor: pointer;
}

.action_machine_active {
  background-color: blue;
}

.action_machine:hover {
  cursor: pointer;
}

.none_machine {
    height: 100%;
    width: 100%;
}

.none_machine:hover {
    cursor: pointer;
}

.factory {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.raw-material-selector {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-content: center;
            align-content: center;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    border: 3px solid black;
    border-radius: 5px;
    background: white;
    height: 100%;
}

.raw-material-selector-item {
    width: 100%;
}

.raw-material {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: left;
          justify-content: left;
  -webkit-align-items: center;
          align-items: center;
}

.raw-material:hover {
  cursor: pointer;
  background: antiquewhite;
}

.raw-material-color {
  height: 20px;
  width: 20px;
  border-radius: 5px;
  border: 2px solid darkslategrey;
}

.raw-material-item {
  margin: 5px;
}

.raw_material-name {
  text-transform: uppercase;
}

.raw-material-popup {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: rgba(0,0,0, 0.5);
}
.raw-material-popup-container {
    position: absolute;
    left: 25%;
    right: 25%;
    top: 25%;
    bottom: 25%;
    margin: auto;
    background: white;
}

